import "./Template.css";
import bearbitzlogo from "../../assets/images/Bearbitz logo.png";
import { Row, Col } from "antd";
import { FaPhone, FaEnvelope, FaWhatsapp } from "react-icons/fa";

const ProductTemplate = ({
  productTitle,
  productImage,
  description,
  tableData,
  BiscuitLogo,
  textColor,
  sectionBgColor,
  productBgcolor,
  tableheadBgcolor,
  tableBgcolor,
  footerBgcolor,
}) => {
  return (
    <div className="product-container">
      <img src={bearbitzlogo} alt="Logo" className="product-logo" />

      <div
        className="template-product-card"
        style={{ background: sectionBgColor }}>
        <Row className="biscuit-description">
          <div className="product-content">
            <Col>
              <h2 className="product-title" style={{ color: textColor }}>
                {productTitle}
              </h2>
              <div className="template-product-image">
                <img
                  src={productImage}
                  alt={productTitle}
                  style={{ background: productBgcolor }}
                />
              </div>
            </Col>
            <Col>
              <table className="product-table">
                <thead>
                  <tr>
                    <th style={{ background: tableheadBgcolor }}>Grams</th>
                    <th style={{ background: tableheadBgcolor }}>
                      Packets in a carton
                    </th>
                    <th style={{ background: tableheadBgcolor }}>
                      Total cartons in a 1x40ft container (Approx.)
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {tableData.map((row, index) => (
                    <tr
                      key={index}
                      style={{
                        borderTop: "3px solid",
                        borderTopColor: textColor,
                        background: tableBgcolor,
                        color: textColor,
                      }}>
                      <td>{row.grams}</td>
                      <td>{row.packets}</td>
                      <td>{row.cartons}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </Col>
          </div>
        </Row>
        <Row>
          <p className="product-description" style={{ color: textColor }}>
            {description}
          </p>
        </Row>
      </div>
      <div className="curve-bg-container">
        <svg
          viewBox="0 0 1440 200"
          xmlns="http://www.w3.org/2000/svg"
          preserveAspectRatio="none"
          className="curve-svg">
          <path
            d="M0,200 C400,0 1040,0 1440,200 L1440,200 L0,200 Z"
            fill={footerBgcolor}
          />
        </svg>
        <svg
          viewBox="0 0 1440 200"
          xmlns="http://www.w3.org/2000/svg"
          preserveAspectRatio="none"
          className="curve-svg-mobile">
          <rect width="1440" height="200" fill={footerBgcolor} />
        </svg>
        <div className="contact-strip">
          <div className="curve-bg">
            <img src={BiscuitLogo} alt="Biscuits" className="biscuit-img" />

            <div className="contact-details">
              <div className="contact-item">
                <FaPhone className="icon" />
                +91 80563 90034
              </div>
              <div className="contact-item">
                <FaWhatsapp className="icon" />
                +91 82485 88257
              </div>
              <div className="contact-item">
                <FaWhatsapp className="icon" />
                +91 89256 34323
              </div>
              <div className="contact-item email">
                <FaEnvelope className="icon" />
                sales@bearbitz.in
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductTemplate;
