import ProductTemplate from "../Biscuits-Template/Template";
import BisImg from "../../assets/products/round creams/orange  biscut.png";
import BiscuitImg from "../../assets/products/round creams/Round-Orange.png";
import { Modal } from "antd";

const BiscuitData = [
  { grams: 13, packets: 300, cartons: 5000 },
  { grams: 14, packets: 300, cartons: 4800 },
  { grams: 20, packets: 120, cartons: 8000 },
  { grams: 22, packets: 300, cartons: 3200 },
  { grams: 25, packets: 300, cartons: 1430 },
  { grams: 25, packets: 6, cartons: 2660 },
  { grams: 30, packets: 100, cartons: 6650 },
  { grams: 33, packets: 100, cartons: 6600 },
];

const OrangeRoundProduct = ({ open, loading, onClose }) => {
  return (
    <Modal loading={loading} open={open} onCancel={onClose} footer={null}>
      <ProductTemplate
        productTitle="ORANGE"
        productImage={BisImg}
        description="Sweet Layers of Joy.Crunchy biscuits filled with rich cream—classic favorites in every shape and flavor. A global snack sensation that delights with every bite."
        tableData={BiscuitData}
        BiscuitLogo={BiscuitImg}
        textColor="#EF5608"
        sectionBgColor="#FFDAC8"
        productBgcolor="#FEB088"
        tableheadBgcolor="#EF5608"
        tableBgcolor="#FFF1EA"
        footerBgcolor="#EF5608"
      />
    </Modal>
  );
};

export default OrangeRoundProduct;
