import ProductTemplate from "../Biscuits-Template/Template";
import BisImg from "../../assets/products/crispy bites/butter  biscut mockup.png";
import BiscuitImg from "../../assets/products/crispy bites/Butter.png";
import { Modal } from "antd";

const BiscuitData = [
  { grams: 280, packets: 12, cartons: 3235 },
  { grams: 340, packets: 12, cartons: 3050 },
  { grams: 453, packets: 12, cartons: 2500 },
];

const ButterProduct = ({ open, loading, onClose }) => {
  return (
    <Modal loading={loading} open={open} onCancel={onClose} footer={null}>
      <ProductTemplate
        productTitle="BUTTER COOKIES"
        productImage={BisImg}
        description="Pure Butter. Pure Bliss. Golden, melt-in-mouth cookies that add richness to every celebration. Ideal for festive gifting and premium export assortments."
        tableData={BiscuitData}
        BiscuitLogo={BiscuitImg}
        textColor="#CA8000"
        sectionBgColor="#FEEFD0"
        productBgcolor="#FDD176"
        tableheadBgcolor="#CA8000"
        tableBgcolor="#FFF9ED"
        footerBgcolor="#CA8000"
      />
    </Modal>
  );
};

export default ButterProduct;
