import ProductTemplate from "../Biscuits-Template/Template";
import BisImg from "../../assets/products/crispy bites/shortbread 1 1.png";
import BiscuitImg from "../../assets/products/crispy bites/Shortbread.png";
import { Modal } from "antd";

const BiscuitData = [
  { grams: 80, packets: 48, cartons: 3280 },
  { grams: 120, packets: 86, cartons: 1100 },
  { grams: 120, packets: 100, cartons: 1000 },
  { grams: 200, packets: 24, cartons: 3000 },
];

const ShortbreadProduct = ({ open, loading, onClose }) => {
  return (
    <Modal loading={loading} open={open} onCancel={onClose} footer={null}>
      <ProductTemplate
        productTitle="SHORT BREAD"
        productImage={BisImg}
        description="Timeless Taste, Elegant Indulgence. Crumbly, buttery classics baked with tradition—perfect for premium moments. Exported in style to meet global demand for quality."
        tableData={BiscuitData}
        BiscuitLogo={BiscuitImg}
        textColor="#733806"
        sectionBgColor="#FDF3DC"
        productBgcolor="#FEFAF1"
        tableheadBgcolor="#733806"
        tableBgcolor="#FEFAF1"
        footerBgcolor="#733806"
      />
    </Modal>
  );
};

export default ShortbreadProduct;
