import ProductTemplate from "../Biscuits-Template/Template";
import BisImg from "../../assets/products/biscuits/NICE BISCUITS 1.png";
import BiscuitImg from "../../assets/products/biscuits/Biscuit-Nice.png";
import { Modal } from "antd";

const BiscuitData = [
  { grams: "27", packets: 120, cartons: 8400 },
  { grams: "36", packets: 240, cartons: 2400 },
  { grams: "40", packets: 120, cartons: 4320 },
];

const NiceProduct = ({ open, loading, onClose }) => {
  return (
    <Modal loading={loading} open={open} onCancel={onClose} footer={null}>
      <ProductTemplate
        productTitle="NICE BISCUITS"
        productImage={BisImg}
        description="Sprinkled with Sweet Simplicity. Golden coconut crunch with a touch of sugar sparkle-pure tea-time nostalgia. Exported for their elegance, enjoyed for their homely charm."
        tableData={BiscuitData}
        BiscuitLogo={BiscuitImg}
        textColor="#004A67"
        sectionBgColor="#C4F7FF"
        productBgcolor="#1AE0FF"
        tableheadBgcolor="#1AE0FF"
        tableBgcolor="#E8FCFF"
        footerBgcolor="#1AE0FF"
      />
    </Modal>
  );
};

export default NiceProduct;
