import ProductTemplate from "../Biscuits-Template/Template";
import BisImg from "../../assets/products/crispy bites/bourbon sugar 1.png";
import BiscuitImg from "../../assets/products/crispy bites/Bourbon.png";
import { Modal } from "antd";

const BiscuitData = [
  { grams: 35, packets: 100, cartons: 6700 },
  { grams: 50, packets: 100, cartons: 4800 },
  { grams: 55, packets: 100, cartons: 4500 },
  { grams: 60, packets: 100, cartons: 4100 },
  { grams: 70, packets: 100, cartons: 3500 },
  { grams: 80, packets: 100, cartons: 3100 },
];

const BourbonProduct = ({
  open,
  loading,
  onClose,
  textColor,
  productBgcolor,
}) => {
  return (
    <Modal loading={loading} open={open} onCancel={onClose} footer={null}>
      <ProductTemplate
        productTitle="CHOCOLATE"
        productImage={BisImg}
        description="Double the Cocoa, Double the Delight. Chocolaty crunch and creamy center—an irresistible treat for all ages. A classic indulgence that travels well and sells fast."
        tableData={BiscuitData}
        BiscuitLogo={BiscuitImg}
        textColor="#68171A"
        sectionBgColor="#FFDDA4"
        productBgcolor="#FFECCB"
        tableheadBgcolor="#68171A"
        tableBgcolor="#FFF2DC"
        footerBgcolor="#68171A"
      />
    </Modal>
  );
};

export default BourbonProduct;
