import ProductTemplate from "../Biscuits-Template/Template";
import BisImg from "../../assets/products/biscuits/MILK biscuit 1.png";
import BiscuitImg from "../../assets/products/biscuits/Biscuit-Milk.png";
import { Modal } from "antd";

const BiscuitData = [
  { grams: "20", packets: 300, cartons: 7050 },
  { grams: "30", packets: 300, cartons: 4750 },
  { grams: "50", packets: 96, cartons: 4100 },
  { grams: "75", packets: 24, cartons: 1220 },
];

const MilkProduct = ({ open, loading, onClose }) => {
  return (
    <Modal loading={loading} open={open} onCancel={onClose} footer={null}>
      <ProductTemplate
        productTitle="MILK BISCUITS"
        productImage={BisImg}
        description="Power-Packed Bites of Energy. Crispy, mildly sweet, and full of goodness perfect for all ages, anytime. Trusted in tea breaks and snack times across the globe."
        tableData={BiscuitData}
        BiscuitLogo={BiscuitImg}
        textColor="#0076D2"
        sectionBgColor="#B5DFFF"
        productBgcolor="#0076D2"
        tableheadBgcolor="#0076D2"
        tableBgcolor="#e2f3ff"
        footerBgcolor="#0076D2"
      />
    </Modal>
  );
};

export default MilkProduct;
