import ProductTemplate from "../Biscuits-Template/Template";
import BisImg from "../../assets/products/rectangle creams/pineapple  biscuit 1.png";
import BiscuitImg from "../../assets/products/rectangle creams/Rectangle-Pineapple.png";
import { Modal } from "antd";

const BiscuitData = [
  { grams: 30, packets: 100, cartons: 7550 },
  { grams: 50, packets: 100, cartons: 4850 },
  { grams: 52.5, packets: 60, cartons: 7700 },
  { grams: 60, packets: 100, cartons: 4100 },
  { grams: 65, packets: 100, cartons: 4100 },
  { grams: 75, packets: 24, cartons: 12100 },
  { grams: 80, packets: 24, cartons: 12000 },
];

const PineappleRectangleProduct = ({ open, loading, onClose }) => {
  return (
    <Modal loading={loading} open={open} onCancel={onClose} footer={null}>
      <ProductTemplate
        productTitle="PINEAPPLE"
        productImage={BisImg}
        description="Sweet Layers of Joy.Crunchy biscuits filled with rich cream—classic favorites in every shape and flavor. A global snack sensation that delights with every bite."
        tableData={BiscuitData}
        BiscuitLogo={BiscuitImg}
        textColor="#629C14"
        sectionBgColor="#E6FFC6"
        productBgcolor="#C5FC7C"
        tableheadBgcolor="#629C14"
        tableBgcolor="#FBFFF6"
        footerBgcolor="#629C14"
      />
    </Modal>
  );
};

export default PineappleRectangleProduct;
