import ProductTemplate from "../Biscuits-Template/Template";
import BisImg from "../../assets/products/rolls/orange  rolls 1.png";
import BiscuitImg from "../../assets/products/rolls/Orange-Roll.png";
import { Modal } from "antd";

const BiscuitData = [
  { grams: 3.5, packets: 24, cartons: 1980 },
  { grams: 16, packets: 16, cartons: 1580 },
  { grams: 18, packets: 8, cartons: 2074 },
  { grams: 19.2, packets: 12, cartons: 1550 },
  { grams: 60, packets: 6, cartons: 2050 },
];

const OrangeRollProduct = ({ open, loading, onClose }) => {
  return (
    <Modal loading={loading} open={open} onCancel={onClose} footer={null}>
      <ProductTemplate
        productTitle="ORANGE"
        productImage={BisImg}
        description="Rolled for Flavor, Baked for Crunch Delicious cylindrical wafer rolls packed with rich cream fillings, offering a delightful crunch with every bite. Available in various tempting flavors, they make for a premium snacking experience and a perfect pairing with beverages. Popular in gift packs and premium retail shelves across global markets."
        tableData={BiscuitData}
        BiscuitLogo={BiscuitImg}
        textColor="#EF5608"
        sectionBgColor="#FFDAC8"
        productBgcolor="#FEB088"
        tableheadBgcolor="#EF5608"
        tableBgcolor="#FFFAF7"
        footerBgcolor="#EF5608"
      />
    </Modal>
  );
};

export default OrangeRollProduct;
