import ProductTemplate from "../Biscuits-Template/Template";
import BisImg from "../../assets/products/waffers/strawberry waffer 1.png";
import BiscuitImg from "../../assets/products/waffers/Strawberry-Waffers.png";
import { Modal } from "antd";

const BiscuitData = [
  { grams: 7, packets: 12, cartons: 475 },
  { grams: 10, packets: 12, cartons: 475 },
  { grams: 14, packets: 12, cartons: 1700 },
  { grams: 18, packets: 12, cartons: 1980 },
  { grams: 20, packets: 12, cartons: 835 },
  { grams: 100, packets: 36, cartons: 3800 },
  { grams: 150, packets: 36, cartons: 3024 },
];

const StrawberryWafferProduct = ({ open, loading, onClose }) => {
  return (
    <Modal loading={loading} open={open} onCancel={onClose} footer={null}>
      <ProductTemplate
        productTitle="STRAWBERRY"
        productImage={BisImg}
        description="Crisp Layers, Creamy Delight Light, crunchy wafer layers filled with smooth cream in flavors like vanilla, chocolate, strawberry, and more. These biscuits offer a perfect bite of crisp and cream, making them a favorite among kids and teens. Easy to pack, easy to love – ideal for export markets worldwide."
        tableData={BiscuitData}
        BiscuitLogo={BiscuitImg}
        textColor="#FF009F"
        sectionBgColor="#FFDBF4"
        productBgcolor="#FFADE6"
        tableheadBgcolor="#FF009F"
        tableBgcolor="#FFF1FB"
        footerBgcolor="#FF009F"
      />
    </Modal>
  );
};

export default StrawberryWafferProduct;
